<template>
    <div class="bg-white pb-12">
        <div class="container md:w-5/12 relative pt-10 m-auto">
            <!-- <form class="grid grid-rows-4 gap-x-3 gap-y-6 w-5/6 justify-center flex-wrap text-xl px-10 py-8 m-auto" -->
            <!-- TITLE -->  
            
            <div @click="visible = !visible" class="flex gap-x-3 justify-center items-center w-[90%] md:w-[70%] border rounded-4xl bg-blue cursor-pointer text-lg text-white m-auto py-2
            md:text-xl">
                <i class="fa fa-search"></i>
                <h1>{{title}}</h1>
                <i class="fa fa-times" v-if="visible"></i>
            </div>  

            <form id="searchForm" 
            :class="{'visible-form': visible}"
            class="hidden w-11/12 md:w-full bg-blue border border-white rounded-3xl text-base tracking-wider shadow-2xl font-semibold m-auto p-4"
            @submit="formSubmit">                
                <div class="flex flex-col gap-y-3 text-center">
                    <Multiselect
                        v-model="formData.destination" 
                        mode="single"
                        :searchable="true"                                               
                        :options="destinations"
                        placeholder="Seleccione destino"      
                        :classes="select_style_opt"
                        @select="updHotelOptions"
                        />
                    <Multiselect
                        v-model="formData.hotel" 
                        mode="single"
                        :searchable="true"                                               
                        :options="hotels"
                        :disabled="disabled"
                        :classes="select_style_opt"
                        placeholder="Seleccione el hotel (opcional)"
                        />
                    <Datepicker 
                    class="datepicker-container"
                    placeholder="Fecha de entrada y salida"
                    v-model="date"
                    :format="'dd/MM/yyyy'"
                    range multi-calendars 
                    auto-apply
                    :enable-time-picker="false"
                    :close-on-auto-apply="true"
                    @update:modelValue="handleDate"
                    />
                        
                    <div class="w-full text-white flex flex-col justify-around items-center gap-6 md:flex-row">
                        <stars v-on:getStar="updStar" :star_init="formData.stars" :star_style="'block'"></stars>

                        <div class="relative w-11/12 border border-white rounded-4xl text-base py-4
                        mt-auto md:px-6 md:py-2">
                            <div class="flex flex-col items-center justify-around gap-x-2
                            md:flex-row">
                                <i class="fas fa-door-open"></i>
                                <span> {{noRooms}} Habitacion </span>
                                <i class="fa fa-user"></i>
                                <span> {{numperson}} Personas </span>
                                <div class="cursor-pointer text-orange text-xl
                                md:ml-auto" @click="visible_person = !visible_person">
                                    <i class="fas fa-edit"></i>
                                </div>
                            </div>
                            <div id="rooms" class="absolute right-0 top-50 bg-white text-darkGray border-lightGray rounded-3xl px-3"
                            :class="{'visible-numperson': visible_person}">
                                <!-- :r="formData.rooms"  -->
                                <num-person 
                                :r="this.formData.rooms" 
                                @close="visible_person=false" 
                                @addRoom="updNumRooms" 
                                @subRoom="updNumRooms">
                                </num-person>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="px-6 pt-4 md:ml-auto">                    
                    <button type="submit" class="px-12 py-1 text-xl text-white uppercase bg-orange rounded-4xl" value="buscar">Buscar</button> 
                </div>
            </form>
        </div>
    </div>
</template>
<script>
import HomeService from "@/services/home.service";

import moment from 'moment'

import Multiselect from '@vueform/multiselect'
import '@vueform/multiselect/themes/default.css'

import NumPerson from '@/components/searcher/NumPerson'
import Stars from '@/components/searcher/Stars'

import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'

export default {
    name: 'SearcherComp',
    props: {
        title: {
            require: false,
            default: 'Busca vacaciones a la medida',
            disabled: true
        }
    },
    components: {
        Multiselect,
        NumPerson,
        Stars,
        Datepicker
    },    
    data() {
        return {                       
            activeTerm: false,
            visible: false,
            visible_person: false,
            default_date: '20-01-022 ~ 20-02-2022',
            select_style_opt: {
                container: 'relative mx-auto w-full flex items-center justify-end box-border cursor-pointer border border-blue rounded-3xl bg-white text-base leading-snug outline-none',
                search: 'w-full absolute inset-0 outline-none focus:ring-0 appearance-none box-border border-8 border-transparent text-base font-sans bg-white rounded-3xl pl-2 rtl:pl-0 rtl:pr-3.5',                                                        
                placeholder: 'flex items-center w-full h-full absolute left-0 top-0 pointer-events-none bg-transparent leading-snug pl-3.5 text-gray-400 rtl:left-auto rtl:right-0 rtl:pl-0 rtl:pr-3.5',
                optionSelected: 'text-white bg-lightGray'
            },
            date: {},
            formData: {
                rooms: [{
                    adults: 1,
                    children: 0,
                    infants: 0,
                }],
                dateRange: {
                    startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                    endDate: moment().add(4, 'days').format('DD MMM YYYY')
                },  
                destination: {
                    value: '',
                    label: ''
                },    
                hotel: {
                    value: '',
                    label: ''
                }, 
                stars: 3           
            },            
            destinations: [],
            hotels: [],
            noRooms: 1,
            }
    },   
    created() {
        this.init()  
    },      
    computed: {
        numperson() {
            let noPerson = 0
            for(let r of this.formData.rooms) {                
                noPerson += r.adults + r.children
            }
            return noPerson
        },        
    },
    methods: {        
        init() {
            HomeService.getSearchInfo().then(list => {                    
                this.destinations = list.destinations
                this.hotels = list.hotels                                
                if(localStorage.getItem('searchform')) {
                    const init_params = JSON.parse(localStorage.getItem('searchform'))                
                    this.date = [new Date(moment(init_params.dateRange.startDate)), new Date(moment(init_params.dateRange.endDate))]
                    this.formData.dateRange = [init_params.dateRange.startDate, init_params.dateRange.endDate]
                    this.formData.destination = init_params.destination
                    this.formData.hotel =  init_params.hotel
                    this.formData.stars = init_params.stars
                    this.formData.rooms = []
                    this.noRooms = init_params.rooms.length
                    for(let e of init_params.rooms) {
                        this.formData.rooms.push({
                            adults: e.adults,
                            children: e.children,
                            infants: e.infants,
                        })
                        
                    } 
                              
                }
            }) 
        },        
        formSubmit(e) {
            e.preventDefault()
            // Get params data to send                                
            localStorage.clear()
            console.log(this.formData)
            localStorage.setItem('searchform', JSON.stringify(this.formData))         
            if(this.$route.fullPath != '/resultado-de-busqueda')
                this.$router.push({name: 'SearchResult'})
            else {
                this.$emit('updparams')
            }
        },
        handleDate(range_date) {
            this.formData.dateRange = {}
            this.formData.dateRange.startDate = moment(range_date[0].toString()).format('YYYY-MM-DD')
            this.formData.dateRange.endDate = moment(range_date[1].toString()).format('YYYY-MM-DD')
        },
        updStar(number) {
            this.formData.starts = number
        },
        updNumRooms(obj) {
            // console.log(obj)
            if(obj.action == 'add') {
                this.formData.rooms.push({
                    adults: 1,
                    children: 0,
                    infants: 0,
                })
            } else if (obj.action == 'del') {
                this.formData.rooms.splice(obj.pos, 1)
            }
            this.noRooms = this.formData.rooms.length                            
        },     
        updHotelOptions(pole_id) {
            this.formData.destination = pole_id
            HomeService.getHotels(pole_id).then(list => {
                
                // desabilita la lista de hoteles en caso que la lista este vacia
                (list.length === 0) ? this.disabled = true : this.disabled= false;

                this.formData.hotel = {}
                this.hotels = []
                this.hotels = list
            })
        },        
    }
}
</script>
<style scoped>       
    /* #searchForm {
        height: 0;
        visibility: collapse;
    } */
    .visible-form {                 
        position: absolute;       
        top: 99%;
        left: 20px;
        z-index: 10;        
        display: block;
        visibility: visible !important;
        /* height: 520px !important; */
        transition: visibility 1200ms;
        transition-timing-function: cubic-bezier(.22,.48,.82,.61);
    }

    #rooms {
        visibility: collapse;
        opacity: 0;
        padding-top: 0.5rem;    
        padding-bottom: 0.5rem;    
        border: 1px solid var(--light-gray);
        z-index: 1000;
    }

    .visible-numperson {                
        visibility: visible !important;
        opacity: 1 !important;
        transition: opacity 150ms;        
        transition-timing-function: cubic-bezier(.22,.48,.82,.61);
    }    
    
</style>